<template>
  <div class="order">
    <div class="xc-title">
      <div class="return" @click="returnHome">
        <img src="../assets/images/return.png" />
      </div>收益报表
    </div>
    <mt-navbar v-model="selected">
      <mt-tab-item id="1" @click.native="chanpin">
        <div class="order-nav-item">类别：<span>{{sureValDouyin}}</span></div>
      </mt-tab-item>
      <mt-tab-item id="2" @click.native="choiceTime('picker1')">
        <div class="order-nav-item">时间：{{value | formatDate}}</div>
      </mt-tab-item>
      <mt-tab-item id="3" @click.native="douyin">
        <div class="order-nav-item">产品：<span>{{sureVal}}</span></div>
      </mt-tab-item>
    </mt-navbar>

    <!-- tab-container -->
    <mt-tab-container v-model="selected1">
      <mt-tab-container-item id="1">
        <div class="article">
          <div class="s-item" v-for="(item,index) in myData.leibiedata" :key="index">
            <div class="title"><img src="../assets/images/home/ic_zuobian.png" />{{item.title}}<img src="../assets/images/home/ic_youbian.png" /></div>
            <div class="section-list">
              <div class="section-item">
                <div class="col">{{item.num1}}</div>
                <div class="con">{{item.detail1}}</div>
              </div>
              <div class="section-item">
                <div class="col">{{item.num2}}</div>
                <div class="con">{{item.detail2}}</div>
              </div>
              <div class="section-item">
                <div class="col">{{item.num3}}</div>
                <div class="con">{{item.detail3}}</div>
              </div>
              <div class="section-item">
                <div class="col">{{item.num4}}</div>
                <div class="con">{{item.detail4}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="detail">
          <div class="title"><img src="../assets/images/home/ic_zuobian.png" />详情<img src="../assets/images/home/ic_youbian.png" /></div>
          <div class="detail-con">
            <div class="detail-item" v-for="(item,index) in datalist" :key="index">
              <div class="d-left"><img :src="item.userimg" /></div>
              <div class="d-middle">
                <div class="col">{{item.username}}</div>
                <div class="con">{{item.datetime}}</div>
              </div>
              <div class="d-right">
                <div class="col">{{item.timu}}</div>
                <div class="con">{{item.money}}</div>
              </div>
            </div>
          </div>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>

    <mt-popup v-model="popupVisible" position="bottom" class="mint-popup" lockScroll="true">
      <mt-picker style="width:100%" :slots="backSlots" value-key="text" showToolbar ref="picker" @change="backPopuphandleConfim">
        <span @click="cancel">取消</span>
        <span @click="backPickerSubBtn">确认</span>
      </mt-picker>
    </mt-popup>

    <mt-popup v-model="popupDouyin" position="bottom" class="mint-popup" lockScroll="true">
      <mt-picker style="width: 100%" :slots="backSlotsDouyin" value-key="text" showToolbar ref="picker" @change="backPopuphandleConfimDouyin">
        <span @click="cancel">取消</span>
        <span @click="backPickerSubBtnDouyin">确认</span>
      </mt-picker>
    </mt-popup>

    <!-- <mt-popup v-model="popupDate" position="middle" class="mint-popup" lockScroll="true">  -->
    <mt-datetime-picker style="width: 100%" class="picker_show" ref="picker1" type="date" v-model="value" @confirm="handleConfirm" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日">
    </mt-datetime-picker>
    <!-- </mt-popup> -->

  </div>
</template>
<script>
import axios from "axios";
import { formatDate } from '../utils/date.js';
export default {
  filters: {                    //时间转换
    formatDate(time) {
      var date = new Date(time);
      return formatDate(date, 'yyyy-MM-dd');
    }
  },
  data() {
    return {
      value: new Date(),          //定义显示时间
      visible: false,
      selected: "1",
      selected1: "1",
      popupVisible: false,
      popupDouyin: false,
      backSlots: [{
        flex: 1,
        defaultIndex: 0,
        values: [],
        className: "slot1",
        textAlign: "center"
      },
      ],
      backSlotsDouyin: [{
        flex: 1,
        defaultIndex: 0,
        values: [],
        className: "slot1",
        textAlign: "center"
      },
      ],
      backval: [],
      sureVal: '',
      backvalDouyin: [],
      sureValDouyin: '',
      detailData: [
        {
          "img": require("../assets/images/home/d-img.png"),
          "name": "这是一个用户名称", "time": "2020-03-03 13:34",
          "desp": "macbook pro电脑",
          "price": "19000.00元"
        },
        {
          "img": require("../assets/images/home/d-img.png"),
          "name": "这是一个用户名称", "time": "2020-03-03 13:34",
          "desp": "macbook pro电脑",
          "price": "19000.00元"
        },
        {
          "img": require("../assets/images/home/d-img.png"),
          "name": "这是一个用户名称", "time": "2020-03-03 13:34",
          "desp": "macbook pro电脑",
          "price": "19000.00元"
        },
        {
          "img": require("../assets/images/home/d-img.png"),
          "name": "这是一个用户名称", "time": "2020-03-03 13:34",
          "desp": "macbook pro电脑",
          "price": "19000.00元"
        }
      ],
      datalist: [],
      myData: {
        leibiedata:[],
        productlist:[],
        leibielist:[]
      },
      slots: [
        {
          flex: 1,
          values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
          className: 'slot1',
          textAlign: 'right'
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        }, {
          flex: 1,
          values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
          className: 'slot3',
          textAlign: 'left'
        }
      ]
    }
  },
  created() {
    //this.$store.dispatch('getTags')

  },
  methods: {
    returnHome() {
      this.$router.push({
        path: "/about"
      });
    },
    getData(chanpin, jintian, douyinhao) {
      axios
        .get("http://dt-sxyj.sxyj.com/ding_data_json.php?datetime=" + jintian + "&procname=" + chanpin + "&leibiename=" + douyinhao)
        .then(res => {
          this.datalist = res.data.datalist;
          this.myData.leibiedata = res.data.leibiedata;
          this.myData.productlist = res.data.productlist;
          this.myData.leibielist = res.data.leibielist;
          //this.myData.datalist = res.data.datalist;


          let date = this.dateFormat("YYYY-mm-dd", new Date());
          if (jintian == 1) {
            this.datalist = this.filterByName(this.datalist, date);
            //console.log('xxxx');
          } else {
            this.datalist = res.data.datalist;
            //console.log('222');
          }

          // 产品
          let productList = res.data.productlist;
          let productListArray = [];
          for (let i = 0; i < productList.length; i++) {
            productListArray.push(productList[i].procname);
          }
          this.backSlots[0].values = productListArray;
          this.sureVal = this.backval.toString();

          // 抖音号
          let douyinList = res.data.leibielist;
          let douyinListArray = [];
          for (let i = 0; i < douyinList.length; i++) {
            douyinListArray.push(douyinList[i].leibiename);
          }
          this.backSlotsDouyin[0].values = douyinListArray;
          this.sureValDouyin = this.backvalDouyin.toString();
          console.log("http://dt-sxyj.sxyj.com/ding_data_json.php?datetime=" + jintian + "&procname=" + chanpin + "&leibiename=" + douyinhao);
        });
    },
    filterByName(aim, name, age) {
      return aim.filter(item => item.name == name || item.age == age)
    },
    dateFormat(fmt, date) {
      let ret;
      var date = new Date(Date.parse(date));
      const opt = {
        "Y+": date.getFullYear().toString(),        // 年
        "m+": (date.getMonth() + 1).toString(),     // 月
        "d+": date.getDate().toString(),            // 日
        "H+": date.getHours().toString(),           // 时
        "M+": date.getMinutes().toString(),         // 分
        "S+": date.getSeconds().toString()          // 秒
        // 有其他格式化字符需求可以继续添加，必须转化成字符串
      };
      for (let k in opt) {
        ret = new RegExp("(" + k + ")").exec(fmt);
        if (ret) {
          fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
        };
      };
      return fmt;
    },
    backPopuphandleConfim(picker, values) {
      this.backval = values;
      if (this.backval != undefined) {
        this.sureVal = this.backval.toString();
      }
    },
    backPopuphandleConfimDouyin(picker, values) {
      this.backvalDouyin = values;
      if (this.backvalDouyin != undefined) {
        this.sureValDouyin = this.backvalDouyin.toString();
      }
    },
    //确认
    backPickerSubBtn() {
      let date = this.dateFormat("YYYY-mm-dd", this.value);
      this.sureVal = this.backval.toString();
      this.popupVisible = false;
      this.getData(this.sureVal, date, this.sureValDouyin);
    },
    backPickerSubBtnDouyin() {
      let date = this.dateFormat("YYYY-mm-dd", this.value);
      this.sureValDouyin = this.backvalDouyin.toString();
      this.getData(this.sureVal, date, this.sureValDouyin)
      this.popupDouyin = false;
    },
    choiceTime(picker1) {
      this.$refs[picker1].open();
    },
    handleConfirm(data) {
      let date = this.dateFormat("YYYY-mm-dd", data);
      this.value = date;
      this.getData(this.sureVal, date, this.sureValDouyin);
    },
    chanpin() {
      this.popupDouyin = true;
    },
    douyin() {
      this.popupVisible = true;
    },
    cancel() {
      this.popupVisible = false;
      this.popupDouyin = false;
    }
  },
  mounted() {
    let date = this.dateFormat("YYYY-mm-dd", this.value);
    this.getData("全部", date, "全部");
  }
}
</script>
<style lang="scss" scoped>
.order {
  .xc-title {
    height: 50px;
    line-height: 50px;
    color: #333;
    text-align: center;
    font-size: 14px;
    background: #fff;
    border-bottom: 1px solid #ddd;
    .return {
      position: absolute;
      left: 20px;
      top: 6px;
      img {
        height: 20px;
      }
    }
  }
  /deep/ .mint-datetime-action {
    width: 43%;
  }
  .v-modal {
    left: 0px;
    bottom: 0px;
    right: 0px;
    top: 0px;
  }
  /deep/ .mint-popup {
    width: 100%;
    height: 230px;
    border-radius: 4px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    /* height:200px;*/

    .picker-item {
      color: #999;
    }
    .picker-selected {
      color: #000;
    }
    .picker-toolbar {
      text-align: right;
      border-bottom: 1px solid #efefef;
      display: flex;
      span {
        background: transparent;
        color: #fc804a;
        font-size: 14px;
        padding-right: 15px;
        line-height: 40px;
        border: 0px;
        outline: none;
        box-shadow: none;
        width: 50%;
        text-align: center;
        display: block;
      }
    }

    .picker-center-highlight {
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;
    }
  }
  .order-title {
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    font-size: 16px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.34);
  }
  /deep/ .mint-navbar {
    background: transparent;
    padding-left: 10px;
    margin-top: 5px;
    .mint-tab-item {
      /*width:95px;*/
      margin-right: 5px;
      flex: initial;
      padding-top:5px;
      padding-bottom:0px;
      .order-nav-item {
        padding-left: 3px;
        padding-right: 3px;
        height: 38px;
        width: 105px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        line-height: 38px;
        border-radius: 20px;
        color: #fff !important;
        background: linear-gradient(
          49deg,
          rgba(253, 102, 74, 1) 0%,
          rgba(252, 128, 74, 1) 100%
        );
        font-size: 12px;
      }
    }
    .mint-tab-item.is-selected {
      .order-nav-item {
        background: rgba(252, 128, 74, 1);
        font-weight: bold;
        color: #333;
      }
      border-bottom: 0px;
    }
  }
  .article {
    background: #fff;
    margin: 10px;
    margin-top:0px;
    .section-list {
      display: flex;
      justify-content: center;
      align-items: center;
      justify-items: center;
      padding-bottom: 10px;
      .section-item {
        flex: 1;
        text-align: center;
        .col {
          color: rgba(233, 63, 108, 1);
          font-size: 16px;
        }
        .con {
          color: rgba(153, 153, 153, 1);
          font-size: 14px;
        }
      }
    }
  }
  .detail {
    background: #fff;
    padding: 10px;
    padding-bottom: 10px;
    border-top:5px solid #efefef;
    .detail-item {
      display: flex;
      margin: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid #bfbfbf;
      .d-left {
        img {
          height: 50px;
          width: 50px;
        }
      }
      .d-middle {
        margin-left: 15px;
        .col {
          color: #333333;
          font-size: 14px;
          padding-top: 5px;
        }
        .con {
          color: #999999;
          font-size: 12px;
          padding-top: 10px;
        }
      }
      .d-right {
        margin-left: 15px;
        .col {
          color: #333333;
          padding-top: 4px;
          font-size: 14px;
        }
        .con {
          color: #e93f6c;
          padding-top: 10px;
          font-size: 14px;
        }
      }
    }
  }
  .title img {
    height: 5px;
    margin-left: 5px;
    margin-right: 5px;
  }
  .title {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-items: center;
    font-size: 16px;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
</style>