    },
    getData(tabsVal) {
      axios
        .get("http://dt-sxyj.sxyj.com/daili_data_json.php?jibiename=" + tabsVal)
        .then(res => {
          let jibielist = res.data.jibielist;
          this.myData.datalist = res.data.datalist;
          this.myData.jibielist=[];
          for(let i=0;i<jibielist.length;i++){
              this.myData.jibielist.push(jibielist[i].jibiename);
          }
           console.log("http://dt-sxyj.sxyj.com/daili_data_json.php?jibiename=" + tabsVal);
           console.log(this.myData.datalist);
        });
    },
    mtNavbar(){
        if(this.selected1==="11"){
            this.getData(this.myData.jibielist[0]);
        }else if(this.selected1==="12"){
            this.getData(this.myData.jibielist[1]);
        }else{
            this.getData(this.myData.jibielist[2]);
        }
    }
  },
  mounted() {
    this.getData("全部");
  },
  components: {}
